import axios from 'axios'
import qs from 'qs'
import { getToken } from '@/utils/local'
import { Dialog } from 'vant'
import Loading from '@/components/loading'

//  VUE_APP_MODE 
const baseURL = {
  dev: '',
  test: '',
  prod: ''
}[process.env.VUE_APP_MODE]

const codeMessage = {
  400: '',
  401: '',
  403: '',
  404: '',
  500: '',
  502: '',
  503: '',
  504: ''
}
const genEmptyPromise = () => new Promise(() => {}) // eslint-disable-line

const getErrorMsg = (error, errorMsg) => {
  let msg = ''
  if (errorMsg) {
    return errorMsg
  }
  // http 
  if (error.response) {
    const { status } = error.response
    return codeMessage[status]
  }
  // 
  if (error.message.includes('timeout')) {
    msg = ''
  } else {
    msg = ''
  }
  return msg || ''
}
const requestStart = (config, loadingCb, showLoading) => {
  loadingCb(true)
  removePending(config) // 
  addPending(config) //  pending 
  config.headers = config.headers || {}
  const token = getToken()
  if (token) {
    config.headers.Authorization = token
  }
  if (showLoading) {
    Loading.open()
  }
}
const requestThenEnd = ({response, loadingCb, showLoading, showWarning, warningMsg, throwWarningError}) => {
  loadingCb(false)
  if (showLoading) {
    Loading.close()
  }
  removePending(response.config) // 
  const responseData = response.data || {}
  if (responseData.success) { // success code
    return responseData.data
  }
  // not success code
  if (showWarning) {
    Dialog({
      message: warningMsg || responseData.msg || '',
      confirmButtonText: ''
    })
  }
  // 
  if (throwWarningError) {
    const err = new Error(JSON.stringify(responseData, null, 2))
    err.name = 'warning'
    return Promise.reject(err)
  }
  return genEmptyPromise()
}
const requestCatchEnd = ({error, loadingCb, showLoading, showError, errorMsg, throwHttpError}) => {
  loadingCb(false)
  if (showLoading) {
    Loading.close()
  }
  if (axios.isCancel(error)) { // 
    console.log('cancel request: ' + error.message)
    return genEmptyPromise()
  }
  if (error.name === 'warning') {
    return Promise.reject(error)
  }
  const msg = getErrorMsg(error, errorMsg)
  if (showError) {
    Dialog({
      message: msg,
      confirmButtonText: ''
    })
  }
  if (error.response) {
    removePending(error.response.config) // 
    const { status } = error.response
    if (status === 401) {
      // ...
    }
  }
  // http
  if (throwHttpError) {
    return Promise.reject(error)
  }
  return genEmptyPromise()
}
/**
 * 
 * @param {Object} params 
 */
const paramsSerializer = params => {
  const data = {}
  for (const k in params) {
    const value = params[k]
    if (value !== '' && value !== null && value !== undefined) {
      data[k] = value
    }
  }
  return qs.stringify(data)
}
//  Map   
const pending = new Map()
/**
 * 
 * @param {Object} config
 */
const addPending = (config) => {
  const url = [
    config.method,
    config.url,
    qs.stringify(config.params),
    qs.stringify(config.data)
  ].join('&')
  config.cancelToken = config.cancelToken || new axios.CancelToken(cancel => {
    if (!pending.has(url)) { //  pending 
      pending.set(url, cancel)
    }
  })
}
/**
 * 
 * @param {Object} config
 */
const removePending = (config) => {
  const url = [
    config.method,
    config.url,
    qs.stringify(config.params),
    qs.stringify(config.data)
  ].join('&')
  if (pending.has(url)) { //  pending 
    const cancel = pending.get(url)
    cancel(url)
    pending.delete(url)
  }
}
/**
 *  pending 
 * @param {Object} config
 */
export const clearPending = () => {
  for (const [url, cancel] of pending) {
    cancel(url)
  }
  pending.clear()
}
const instance = axios.create({
  baseURL,
  //  params url 
  paramsSerializer
})
/**
 * @param {Object} options 
 * @param {Boolean} [options.showWarning=true] 
 * @param {Boolean} [options.showError=true] httphttp
 * @param {Boolean} [options.showLoading=true]  loading
 * @param {Function} [options.loadingCb=()=>{}] loading 
 * @param {Boolean} [options.throwWarningError=false] 
 * @param {Boolean} [options.throwHttpError=false] httphttp
 * @param {String} [options.warningMsg=''] 
 * @param {String} [options.errorMsg=''] http
 * @return {Promise} Promise
 */
const request = (
  {
    showWarning = true,
    showError = true,
    showLoading = true,
    loadingCb = () => {}, // eslint-disable-line
    throwWarningError = true,
    throwHttpError = true,
    warningMsg = '',
    errorMsg = '',
    ...options
  } = {}
) => {
  requestStart(options, loadingCb, showLoading)
  return instance(options)
    .then(response => {
      return requestThenEnd({response, loadingCb, showLoading, showWarning, warningMsg, throwWarningError})
    })
    .catch(error => {
      console.log('request catch', error)
      return requestCatchEnd({error, loadingCb, showLoading, showError, errorMsg, throwHttpError})
    })
}
export default request

export const get = (url, params = {}, options = {}) => {
  const result = {
    method: 'get',
    url,
    params,
    ...options
  }
  return request(result)
}

export const post = (url, data = {}, options = {}) => {
  const result = {
    method: 'post',
    url,
    data,
    ...options
  }
  return request(result)
}
